/* General Styles */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(0.3125em + 1vmin); /* Changed from 5px to em */
  letter-spacing: -0.5px;
  line-height: 0.3;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Main Container */
main {
  background-color: hsl(46, 100%, 84%);
  display: flex;
  flex-direction: column; /* Stack children vertically */
  justify-content: flex-start;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  overflow: visible; /* Allow expansion */
  min-height: fit-content;
  padding: 1.875em; /* Changed from 30px to em */
}

@media screen and (max-width: 768px) {
  main {
    padding: 0.9375em; /* Changed from 15px to em */
    max-width: 95%;
  }
}

/* Calendar Grid */
.calendar {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 3%;
  justify-items: center;
  width: 100%;
  box-sizing: border-box;
  max-width: 100%;
  padding-bottom: 3.75em; /* Changed from 60px to em */
}

.calendar-day {
  background: white;
  color: black;
  padding: 0.3125em; /* Changed from 5% if applicable or adjust as needed */
  border-radius: 5px;
  width: 90%;
  text-align: center;
  border: 1px solid black;
  transition: padding-bottom 0.3s ease;
  padding-bottom: 0.625em; /* Changed from 10px to em */
  position: static; /* Remove relative positioning */
  display: flex; /* Enable flex layout */
  flex-direction: column; /* Stack children vertically */
  align-items: center; /* Center align items horizontally */
}

.calendar-day.bucket {
  background-color: rgb(165, 241, 255);
}

.calendar-day.lght {
  background-color: yellow;
}

.calendar-day.regular {
  background-color: rgb(255, 195, 195);
}

.calendar-day.weekday {
  font-weight: 500;
  border: none;
  background-color: hsl(51, 68%, 51%);
}

/* Date Styles */
.date {
  font-weight: normal;
}

.weekday-date {
  font-weight: bold;
}

/* Events */
.events {
  margin-top: 0.3125em; /* Changed from 5px to em */
}

.event {
  font-weight: bold;
  margin-top: 0.3125em; /* Changed from 5px to em */
  font-size: 0.7em;
}

@media screen and (min-width: 1024px) {
  .calendar-day {
    width: 90%;
    font-size: 1em;
  }
}

@media screen and (max-width: 768px) {
  .calendar-day {
    width: 90%;
    padding: 5%;
    font-size: 0.8em;
  }

  .schedule-details {
    font-size: 0.7em;
  }
}

/* Schedule Details */
.schedule-details {
  margin-top: 0.3125em; /* Changed from 5px to em */
  font-size: 1em;
  border-top: 1px solid #ccc;
  padding: 0.3125em; /* Changed from 5px to em */
  background-color: hsl(47, 100%, 50%);
  border: 1px solid black;
  border-radius: 0 0 5px 5px;
  width: 85%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  position: static; /* Ensure it stays within the flow */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Optional: Enhance visibility */
}

/* Place and Organizer */
.place,
.organizer {
  margin-top: 0.1875em; /* Changed from 3px to em */
}

/* Highlight */
.highlight {
  color: hsl(46, 73%, 60%);
}

/* Footer Styles */
.footer,
.footer2 {
  text-align: center;
  margin-top: 2%;
  margin-left: 2%;
  width: 20%;
  border: 1px solid black;
  font-size: 0.9rem;
  vertical-align: top;
}
